@charset "UTF-8";

@import "import/lib-fonts";
@import "base";
@import "import/typography";
@import "import/reset-css";
@import "component/base";

// common
.blind { display: none; overflow: hidden; border: 0; position: relative; z-index: -1; width: 1px; height: 1px; clip: rect(1px, 1px, 1px, 1px); clip-path: inset(50%);}

.pc-only{display: block !important;}
.mo-only{display: none !important;}

.text-line{overflow: hidden; text-overflow: ellipsis; white-space: nowrap;}

.flex-end{@include flex('', 'flex-end !important');}
.flex-center{@include flex('center !important');}
.flex-start{@include flex('flex-start !important', '');}

.ml-auto{margin-left: auto !important;}

.roboto{font-family: $roboto;}

//space
$position: (t:top, b:bottom, r:right, l: left);
$total: 10;
@each $abbr, $pos in $position {
  @for $i from 0 to $total {
    .m#{$abbr}-#{$i} {margin-#{$pos}: $i*10px !important;}
    .p#{$abbr}-#{$i} {padding-#{$pos}: $i*10px !important;}
  }
}
@media all and (max-width:750px){
  @each $abbr, $pos in $position {
    @for $i from 0 to $total {
      .m#{$abbr}-#{$i} {margin-#{$pos}: $i*1.39vw !important;}
      .p#{$abbr}-#{$i} {padding-#{$pos}: $i*1.39vw !important;}
    }
  }
}

//hr
hr.line{display: block; margin: 30px 0; width: 100%; height: 1px; background-color: $c-gray4;}

//width
.w-full{width: 100% !important;}
.w-550{max-width: 550px;}
.w-860{margin: 0 auto; max-width: 860px;}

//ico
.ico{display: inline-block; width: 20px; height: 20px;}
.ico-alarm{@include bg('common/ico_alarm.svg');}
.ico-user{@include bg('common/ico_user.svg');}
.ico-more{@include bg('common/ico_arrow_right.svg');}
.ico-down{@include bg('common/ico_download.svg');}

.btn-ico .ico{position: relative; width: 30px; height: 30px; background-repeat: no-repeat; background-position: center; background-size: 100% auto; z-index: 2;}
.btn-ico .ico + .ico{margin-left: 16px;}
.btn-ico .ico-copy{background-image: url('../img/common/ico_copy2.svg');}
.btn-ico .ico-modify{background-image: url('../img/common/ico_modify.svg');}
.btn-ico .ico-delete{background-image: url('../img/common/ico_delete.svg');}
.btn-ico .ico-down{background-size: 20px auto;}

//color
.c-blue{color: $c-blue !important;}
.c-red{color: $c-red !important;}
.c-black{color: $c-black !important;}

//text-style
.bold{font-weight: 700 !important;}

.tit-con{margin-bottom: 30px;}
.tit-con [class*=tit]{margin-bottom: 12px;}
.tit-l{@include flex('space-between'); margin-bottom: 30px; font-size: 28px; font-weight: 700; color: $c-black;}
.tit-l span{@include flex();}
.tit-l small{margin-left: 20px; font-size: 20px; font-weight: 400; color: $c-gray2;}
.tit-l .desc{font-size: 16px; font-weight: 400; color: $c-gray;}
.tit-l .desc .num{margin-right: 4px; font-family: $roboto; font-weight: 700;}
.tit-m{@include flex('space-between'); margin-bottom: 30px; font-size: 22px; font-weight: 700; color: $c-black;}
.tit-m .desc{font-size: 16px; font-weight: 400; color: $c-gray;}
.tit-m .desc .num{margin-right: 4px; font-family: $roboto; font-weight: 700;}
.tit-m small{margin-left: 12px; font-size: 16px; font-weight: 400;}

//notice-list
.notice-list{}
.notice-list li{position: relative; padding-left: 12px; font-size: 16px; color: $c-gray; line-height: 1.4;}
.notice-list li::before{content: '*'; position: absolute; top: 3px; left: 0;}
.notice-list li + li{margin-top: 8px;}

//box
.white-box{padding: 40px; background-color: $c-white; border-radius: 20px;}
.white-box + .white-box{margin-top: 30px;}
.white-box.padding-l{padding-top: 80px; padding-bottom: 80px;}

//grid
[class*=grid]{display: grid;}
.grid-l{gap: 60px;}
.grid-m{gap: 40px;}
.grid-s{gap: 20px;}

.flex-column{display: flex;}
.grid.col-60{grid-template-columns: 60.25% auto; gap: 20px;}
.grid.col-60 .white-box + .white-box{margin-top: 0;}

//paging
.paging{@include flex('center'); padding-top: 50px; font-family: $roboto;}
.paging .page-list{@include flex(); margin: 0 10px;}
.paging .page-list button{padding-top: 2px; width: 30px; height: 30px; font-size: 16px; color: $c-gray; border-radius: 40px;}
.paging .page-list button + button{margin-left: 6px;}
.paging .page-list button.on{background-color: $c-primary; color: #fff;}
.paging > button{width: 30px; height: 30px; background-repeat: no-repeat; background-position: center; background-size: 20px auto;}
.paging > button.prev{background-image: url('../img/common/ico_arrow_prev.svg');}
.paging > button.next{background-image: url('../img/common/ico_arrow_next.svg');}
.paging > button.prev-more{background-image: url('../img/common/ico_arrow_prev2.svg');}
.paging > button.next-more{background-image: url('../img/common/ico_arrow_next2.svg');}

//tab-list
.tab-list{position: relative; @include flex(); margin-bottom: 20px; }
.tab-list::after{content:''; display: block; position: absolute; bottom: 0; left: 0; width: 100%; height: 1px; background-color: $c-gray4;}
.tab-list li{margin-right: 40px;}
.tab-list li:last-child{margin-right: 0;}
.tab-list li button{position: relative;  padding-bottom: 10px; width: 100%; font-size: 20px; font-weight: 700; color: $c-gray2;}
.tab-list li.active button{color: $c-primary;}
.tab-list li.active button::after{content: ''; display: block; position: absolute; bottom: 0; left: 0; width: 100%; height: 3px; background-color: $c-primary; z-index: 2;}
.tab-con{display: none;}
.tab-con.active{display: block;}

//location
.location{margin-bottom: 30px;}
.location span{font-size: 16px;}
.location span::after{content: ''; display: inline-block; margin: 0 12px; width: 12px; height: 12px; @include bg('common/ico_arrow_right_s.svg');}
.location span:last-child::after{display: none;}

//table-style
.table{overflow: auto; border-top: 1px solid $c-black;}
.table.wid-l table{min-width: 850px;}
.table.wid-m{}
.table.wid-s{}

.table table{width: 100%; text-align: center;}
.table table tr{height: 60px; border-bottom: 1px solid $c-gray4; font-size: 16px;}
.table table th{font-weight: 700;}
.table table td{color: $c-gray; overflow: hidden; white-space: nowrap; text-overflow: ellipsis;}
.table table tbody tr:hover{background-color: $c-bg;}

.table .btn-con{justify-content: center; align-items: center; height: 60px;}

.no-data{@include flex('center'); flex-direction: column; height: 380px; border-bottom: 1px solid $c-gray4; border-top: 1px solid $c-black;}
.no-data p{font-size: 18px; line-height: 1.4; text-align: center;}
.no-data p.btn-con{align-items: center;}
.no-data p.btn-con br{display: none;}
.no-data .img-btn{flex-shrink: 0; margin-right: 4px; padding: 7px 20px; font-size: 12px; font-weight: 700; color: $c-white; background: $c-primary; border-radius: 30px;}

@media (max-width: 1200px) {
  .flex-column{flex-wrap: wrap;}
  .grid.col-60{grid-template-columns: auto; gap: 20px;}
}

@media (max-width: 960px) {
  .paging .page-list{margin: 0 20px;}
  .paging .page-list button + button{margin-left: 12px;}
}

@media (max-width: 720px){
  //hr
  hr.line{margin: 5.56vw 0; height: 0.28vw;}

  .tit-con{margin-bottom: 5.56vw;}
  .tit-con [class*=tit]{margin-bottom: 2.78vw;}
  .tit-l{font-size: 5.28vw;}
  .tit-l small{margin-left: 4.17vw; font-size: 4.17vw;}
  .tit-m{font-size: 4.44vw;}
  .tit-m small{margin-left: 1.67vw; font-size: 3.61vw;}
  .tit-l .desc{font-size: 3.61vw;}
  .tit-l .desc .num{margin-right: 1.39vw;}
  .tit-m .desc{font-size: 3.61vw;}
  .tit-m .desc .num{margin-right: 1.39vw;}

  //notice-list
  .notice-list li{padding-left: 2.78vw; font-size: 3.61vw;}
  .notice-list li::before{top: 0.69vw;}
  .notice-list li + li{margin-top: 1.11vw;}

  //box
  .white-box{padding: 8.33vw 4.17vw; border-radius: 2.78vw;}
  .white-box.padding-l{padding-top: 16.67vw; padding-bottom: 16.67vw;}
  .white-box + .white-box{margin-top: 4.17vw;}

  //grid
  .grid-l{gap: 11.11vw;}
  .grid-m{gap: 8.33vw;}
  .grid-s{gap: 5.56vw;}

  .grid.col-50{gap: 8.33vw;}
  .grid.col-60{gap: 5.56vw;}

  //icon
  .ico{width: 5.56vw; height: 5.56vw;}
  .btn-ico .ico{width: 6.94vw; height: 6.94vw;}
  .btn-ico .ico + .ico{margin-left: 2.78vw;}

  //tab-list
  .tab-list{margin-bottom: 4.17vw;}
  .tab-list::after{height: 0.28vw;}
  .tab-list li{margin-right: 5.56vw;}
  .tab-list li button{padding-bottom: 2.78vw; font-size: 4.17vw;}
  .tab-list li.active button{color: $c-primary;}
  .tab-list li.active button::after{content: ''; display: block; position: absolute; bottom: 0; left: 0; width: 100%; height: 0.56vw; background-color: $c-primary;}
  .tab-con{display: none;}
  .tab-con.active{display: block;}

  .location{display: none;}

  //paging
  .paging{padding-top: 8.33vw;}
  .paging .page-list{margin: 0 2.78vw;}
  .paging .page-list button{padding-top: 0.56vw; width: 8.33vw; height: 8.33vw; font-size: 3.61vw; border-radius: 5.56vw;}
  .paging .page-list button + button{margin-left: 2.78vw;}
  .paging > button{width: 8.33vw; height: 8.33vw; background-size: 5.56vw auto;}

  //table-style
  .table{border-top-width: 0.28vw;}
  .table.wid-l table{min-width: 194.44vw;}
  .table.wid-m{}
  .table.wid-s{}

  .table table tr{height: 11.11vw; border-bottom-width: 0.28vw; font-size: 3.61vw;}

  .table .btn-con{height: 11.11vw;}

  .no-data{height: 69.44vw; border-bottom-width: 0.28vw;}
  .no-data p{font-size: 3.89vw;}
  .no-data p.btn-con{align-items: flex-start;}
  .no-data p.btn-con br{display: block;}
  .no-data .img-btn{margin-right: 1.39vw; padding: 1.39vw 1.94vw; font-size: 2.78vw; border-radius: 4.17vw;}

}