@charset "UTF-8";

@import "../base";

//btn-style
.btn-con{display: flex; justify-content: center;}
.btn-con .btn + .btn{margin-left: 20px;}
.btn-con .btn-l{margin-top: 40px;}

.btn-icon .btn{position: relative; width: 272px; height: 52px; font-size: 20px; font-weight: 700; border-radius: 26px;}
.btn-icon .btn::before{content: ''; display: inline-block; margin-top: -3px; margin-right: 20px; width: 22px; height: 22px;}
.btn-icon .btn-notice::before{ @include bg('common/btn_notice.svg');}

.btn{display: inline-flex; align-items: center; justify-content: center; width: fit-content;}
.btn-txt{@include flex(); height: fit-content; font-size: 16px; font-weight: 500; color: $c-black;}
.btn-txt.underline{text-decoration: underline;}
.btn-line{border: 1px solid $c-primary; background-color: $c-white;}
.btn-line.white{color: $c-white; border-color: $c-white; background-color: transparent;}
.btn-line.disable{color: $c-gray2; border-color: $c-gray4;}

.btn-xs{padding: 0 8px; height: 30px; font-size: 12px; font-weight: 500; color: $c-black; border-radius: 4px;}
.btn-s{width: 60px; height: 50px; font-size: 12px; font-weight: 700; border-radius: 10px;}
.btn-m{width: 140px; height: 40px; font-size: 16px; font-weight: 700; border-radius: 30px;}
.btn-l{width: 160px; height: 50px; font-size: 16px; font-weight: 700; border-radius: 30px;}

.btn.bg-white{color: $c-black; background-color: $c-white;}
.btn.bg-black{color: $c-white; background-color: $c-black;}
.btn.bg-red{color: $c-white; background-color: $c-red;}
.btn.bg-blue{color: $c-white; background-color: $c-blue;}
.btn.bg-yellow{color: $c-white; background-color: $c-yellow;}

.btn.wid-s{padding: 0 30px; width: auto;}
.btn.wid-m{padding: 0 30px; width: auto; min-width: 180px;}
.btn.wid-l{width: 100%;}

.btn.square{border-radius: 10px;}

@media (max-width: 960px) {

}

@media all and(max-width: 720px){
  //.btn-con{}
  .btn-con .btn + .btn{margin-left: 2.78vw;}
  .btn-con .btn-l{margin-top: 6.94vw;}
  //.btn-icon .btn{width: 37.78vw; height: 7.22vw; font-size: 2.78vw; border-radius: 3.61vw;}
  //.btn-icon .btn::before{content: ''; display: inline-block; margin-top: -0.42vw; margin-right: 2.78vw; width: 3.06vw; height: 3.06vw;}
  //.btn-icon .btn-notice::before{ @include bg('common/btn_notice.svg');}

  .btn-txt{font-size: 3.61vw;}
  .btn-line{border-width: 0.28vw;}

  .btn-xs{padding: 0 1.67vw; height: 6.94vw; font-size: 3.06vw; border-radius: 1.39vw;}
  .btn-s{width: 100%; height: 11.11vw; font-size: 3.06vw; border-radius: 1.39vw;}
  .btn-m{width: 25.00vw; height: 8.33vw; font-size: 3.61vw; border-radius: 4.17vw;}
  .btn-l{width: 100%; height: 11.11vw; font-size: 3.61vw; border-radius: 8.33vw;}

  .btn.wid-s{}
  .btn.wid-m{padding: 0 4.17vw; width: 100%; min-width: auto;}
  .btn.wid-l{width: 100%;}
}

