@charset "UTF-8";

@import "../base";

//input
input[type="file"]{display: none;}
input::placeholder, textarea::placeholder{ font-size: inherit; color: $c-gray2;}

.inp-con{@include flex();}
.inp-con .notice-txt{position: relative; margin-top: 30px; padding-left: 20px; font-size: 14px; font-weight: 300; line-height: 1.4; word-break: keep-all;}
.inp-con .notice-txt::before{content: '※'; position: absolute; left: 0;}

.inp-list{display: grid; gap: 20px;}
.inp-list.col{grid-template-columns: repeat(4, 1fr); gap: 20px 60px;}
.inp-list.col .inp-tit,
.inp-list.col .inp-label{width: 100px; padding-right: 10px;}
.inp-tit{width: 160px; font-size: 16px; font-weight: 700; color: $c-black; flex-shrink: 0;}

.inp{display: flex; width: 100%;}

.inp.readonly .inp-group{background: $c-gray5;}
.inp-tel.readonly .inp-group{background: transparent;}
.inp-tel.readonly .inp-group input{background: $c-gray5;}
.inp.error .inp-group{border-color: $c-red;}
.inp.txt-r .inp-group input{text-align: right;}

.inp .inp-label{margin-top: 16px; width: 160px; font-size: 16px; font-weight: 700; color: $c-black; text-align: left; flex-shrink: 0; cursor: initial;}
.inp .inp-area{@include flex(); flex-wrap: wrap; width: 100%; }
.inp .inp-area .inp-group + .inp-group{margin-left: 10px;}
.inp .inp-group{@include flex(); padding: 0 20px; width: 100%; height: 50px; font-size: 16px; border: 1px solid $c-gray4; border-radius: 10px;  background: #fff;}
.inp .inp-group input{width: 100%; font-size: 16px; font-weight: 700;}
.inp .inp-group input::placeholder{font-weight: 400; color: $c-gray2;}
.inp .inp-group.text-area{padding: 20px; height: 100px;}
.inp .inp-group.text-area textarea{width: 100%; height: 100%; text-align: left;}

//.inp .search .btn-search{display: inline-block; width: 24px; height: 24px; @include bg('common/ico_search.svg'); flex-shrink: 0;}
.inp .dash{content: ''; display: block; margin: 0 8px; width: 5px; height: 2px; background-color: $c-black; flex-shrink: 0;}

.inp-tel .inp-group{padding: 0; border: none;}
.inp-tel .inp-group input{padding: 0 20px; width: 100%; height: 100%; border: 1px solid $c-gray4; border-radius: 10px; background: #fff;}
.inp-text-area{align-items: center;}
.inp-text-area .inp-label{margin-top: 0;}
.inp-text-area .inp-group{padding: 20px; height: 100px;}
.inp-text-area .inp-group textarea{width: 100%; height: 100%; text-align: left; word-break: break-all;}

.inp .ico{width: 24px; height: 24px; flex-shrink: 0;}
.inp .ico ~ input{margin-left: 4px;}
.inp .ico-search{@include bg('common/ico_search.svg');}
.inp .ico-cal{@include bg('common/ico_calendar.svg');}
.inp .ico-upload{@include bg('common/ico_upload.svg');}

.inp .msg-desc{position: relative; margin-top: 10px; padding-left: 6px; width: 100%; font-size: 12px; color: $c-gray;}
.inp .msg-desc::before{content: ''; display: inline-block; position: absolute; top: 6px; left: 0; width: 3px; height: 3px; border-radius: 10px; background-color: $c-gray;}
.inp .msg-desc + .msg-desc{margin-top: 6px;}
.inp .msg-error{position: relative; margin-top: 10px; padding-left: 6px; width: 100%; font-size: 12px; color: $c-red;}
.inp .msg-error::before{content: '*'; position: absolute; top: 3px; left: 0;}
.inp .msg-error + .msg-error{margin-top: 6px;}

//checkbox
.inp-chk input{@extend .blind;}
.inp-chk input ~ label{display: inline-flex; align-items: center;}
.inp-chk input ~ label::before{content: ''; display: inline-block; width: 20px; height: 20px; @include bg('common/ico_check_off.svg'); flex-shrink: 0;}
.inp-chk input ~ label .chk-txt{margin-left: 8px; font-size: 16px; color: $c-gray;}
.inp-chk input:checked ~ label::before{background-image: url('../img/common/ico_check_on.svg');}
.inp-chk input:checked ~ label .chk-txt{font-weight: 500; color: $c-primary;}

//select
.select-con{@include flex(); }
.select-con .tit{width: 160px; font-size: 16px; font-weight: 700; color: $c-black; flex-shrink: 0;}

.inp-select{@include flex();}
.inp-select .select{position: relative; @include flex('space-between'); padding: 0 20px; width: 100%; height: 100%; font-size: 16px; color: $c-gray2; border: 1px solid $c-gray4; border-radius: 10px; cursor: pointer; background-color: #fff; z-index: 2;}
.inp-select .select::after{content: ''; display: inline-block; width: 20px; height: 20px; @include bg('common/ico_arrow_down.svg'); flex-shrink: 0;}
.inp-select .select .selected-value{overflow: hidden; white-space: normal; text-overflow: ellipsis; display: -webkit-box; -webkit-line-clamp: 1; -webkit-box-orient: vertical; word-break: keep-all; padding-right: 10px; width: 100%;text-align: left;}
.inp-select .select .selected-value.bold{font-weight: 700; color: $c-black;}
.inp-select .list{display: none; position: absolute; top: 40px; padding: 20px 10px; width: 100%; font-size: 16px; line-height: 1.8; text-align: left; border: 1px solid $c-gray4; border-radius: 0 0 10px 10px; z-index: 1; background: #fff;}
.inp-select .list li{@include flex('space-between'); padding: 5px 10px; border-radius: 5px;}
.inp-select .list .option{cursor: pointer;}
.inp-select .list .no-data-txt p{padding-top: 10px; width: 100%; font-size: 16px; text-align: center;}

.inp-select.inp-scroll .list{overflow: hidden auto; height: 120px;}

.inp-select .select-area{position: relative; width: 100%; height: 50px;}

.inp-select.on .select-area{z-index: 5;}
//.inp-select.on .select{background: $c-primary; color: #fff;}
.inp-select.on .select::after{transform: rotate(180deg);}
.inp-select.on .list{display: block;}

//search-form
.search-form{text-align: center;}
.search-form.white-box{padding: 70px;}
.search-form .inp-con{margin: 0 auto; width: 70%;}
.search-form .tit-l{justify-content: center; margin-bottom: 40px;}

.search-form .inp-select .select-area{position: relative; width: 160px; flex-shrink: 0;}
.search-form .inp-select ~ .inp{margin-left: 10px;}

.search-form .recommend{@include flex('center'); margin-top: 24px;}
.search-form .recommend strong{margin-right: 30px; font-size: 14px; font-weight: 700; flex-shrink: 0;}
.search-form .recommend .tag-list{@include flex();}
.search-form .recommend .tag{margin-right: 12px; }
.search-form .recommend .tag:last-child{margin-right: 0;}
.search-form .recommend .tag a{display: inline-flex; align-items: center; padding: 0 16px; height: 32px; font-size: 12px; font-weight: 700; border: 1px solid $c-gray4; border-radius: 30px;}
.search-form .recommend .tag a:hover{border-color: $c-black;}
//.search-form .recommend .tag a:hover{background-color: $c-black; color: $c-white;}

.search-form .sns-con{@include flex('center'); margin-top: 24px;}
.search-form .sns-con strong{margin-right: 30px; font-size: 14px; font-weight: 700; flex-shrink: 0;}
.search-form .sns-con .sns-list{margin-top: 0;}
.search-form .sns-con .sns-list li + li{margin-left: 10px;}
.search-form .sns-con .sns-list li a{width: 32px; height: 32px;}

//inp-radio-con
.inp-radio-con{@include flex();}
.inp-radio-con .inp-wrap{width: 100%;}
.inp-radio-con .inp-con + .inp-con{margin-top: 20px;}
.inp-radio-con .inp-con .inp-radio{min-width: 170px;}
.inp-radio-con .inp-con .btn-con .btn-line{margin-top: 0; padding: 0 26px; width: auto; color: $c-gray2; border-color: $c-gray4; text-align: center; word-break: keep-all;}
.inp-radio-con .inp-con .btn-con .btn-line.active{color: $c-primary; border-color: $c-primary;}
.inp-radio-con.col .inp-radio + .inp-radio{margin-left: 40px;}

.inp-radio input{@extend .blind;}
.inp-radio .inp-label{@include flex(); font-size: 16px; color: $c-gray;}
.inp-radio .inp-label::before{content: ''; display: inline-block; margin-right: 10px; width: 20px; height: 20px; @include bg('common/ico_radio.svg');}
.inp-radio input:checked ~ .inp-label{font-weight: 500; color: $c-black;}
.inp-radio input:checked ~ .inp-label::before{background-image: url('../img/common/ico_radio_on.svg');}
.inp-radio .txt{margin-top: 5px; padding-left: 30px; font-size: 12px; color: $c-gray;}

@media (max-width: 1450px) {
  .inp-list.col{grid-template-columns: repeat(2, 1fr);}
}

@media (max-width: 1200px) {
  .inp-radio-con .inp-con{flex-wrap: wrap;}
  .inp-radio-con .inp-con .inp-radio{margin-bottom: 10px; width: 100%;}

  .inp-list.col{grid-template-columns: auto;}
}

@media (max-width: 960px) {
  .nav{display: none;}

  .search-form .inp-con{width: 100%;}
}

@media all and(max-width: 720px){
  //input
  .inp-con .notice-txt{margin-top: 4.17vw; padding-left: 0; font-size: 3.33vw;}

  .inp-list{display: grid; gap: 5.56vw;}
  .inp-tit{margin-bottom: 2.78vw; width: 100%; font-size: 3.61vw;}
  .inp-list.col{gap: 5.56vw;}
  .inp-list.col .inp-tit,
  .inp-list.col .inp-label{width: 100%; padding-right: 0;}

  .inp{flex-wrap: wrap;}
  .inp .inp-label{margin-top: 0; margin-bottom: 2.78vw; width: 100%; font-size: 3.61vw;}
  .inp .inp-area .inp-group + .inp-group{margin-left: 1.39vw;}
  .inp .inp-group{padding: 0 2.78vw; height: 11.11vw; font-size: 3.61vw; border-width: 0.28vw; border-radius: 1.39vw;}
  .inp .inp-group input{font-size: 3.61vw;}

  .inp .dash{margin: 0 2.36vw; width: 1.53vw; height: 0.42vw; background-color: $c-black; flex-shrink: 0;}

  .inp-tel .inp-group{padding: 0;}
  .inp-tel .inp-group input{padding: 0 2.78vw; border-width: 0.28vw; border-radius: 1.39vw;}
  .inp-text-area .inp-group{padding: 2.78vw; height: 33.33vw;}

  .inp .ico{width: 5.56vw; height: 5.56vw;}
  .inp .ico ~ input{margin-left: 1.39vw;}

  .inp .msg-desc{margin-top: 2.78vw; padding-left: 1.67vw; font-size: 3.06vw;}
  .inp .msg-desc::before{top: 1.39vw; width: 0.83vw; height: 0.83vw; border-radius: 1.39vw; }
  .inp .msg-desc + .msg-desc{margin-top: 1.67vw;}
  .inp .msg-error{margin-top: 2.78vw; padding-left: 1.67vw; font-size: 3.06vw;}
  .inp .msg-error::before{top: 0.69vw;}
  .inp .msg-error + .msg-error{margin-top: 1.67vw;}

  //checkbox
  .inp-chk input ~ label::before{width: 5.56vw; height: 5.56vw;}
  .inp-chk input ~ label .chk-txt{margin-left: 1.67vw; font-size: 3.61vw; line-height: 1.4;}

  //select
  .inp-select{flex-wrap: wrap; width: 100%;}
  .inp-select .select{padding: 0 2.78vw; font-size: 3.61vw; border-width: 0.28vw; border-radius: 1.39vw;}
  .inp-select .select::after{width: 5.56vw; height: 5.56vw;}
  .inp-select .select .selected-value{padding-right: 1.39vw;}
  .inp-select .list{top: 9.72vw; padding: 4.17vw 2.78vw; font-size: 3.61vw; border-width: 0.28vw; border-radius: 0 0 1.39vw 1.39vw;}
  .inp-select .list li{padding: 0.69vw 1.39vw; border-radius: 0.69vw;}
  .inp-select .list .no-data-txt p{padding-top: 2.78vw; font-size: 3.61vw;}
  
  .inp-select.inp-scroll .list{height: 25.00vw;}

  .inp-select .select-area{height: 11.11vw;}

  //search-form
  .search-form.white-box{padding: 11.11vw 4.17vw;}
  .search-form .inp-con{flex-wrap: wrap; width: 100%;}
  .search-form .tit-l{margin-bottom: 8.33vw;}

  .search-form .inp-select .select-area{width: 100%; }
  .search-form .inp-select ~ .inp{margin-left: 0; margin-top: 2.78vw;}

  .search-form .recommend{flex-direction: column; margin-top: 6.94vw;}
  .search-form .recommend strong{margin-right: 0; margin-bottom: 4.17vw; font-size: 3.33vw;}
  .search-form .recommend .tag-list{flex-wrap: wrap; width: 80%; justify-content: center;}
  .search-form .recommend .tag{margin-right: 2.78vw;}
  .search-form .recommend .tag:nth-child(3){margin-right: 0};
  .search-form .recommend .tag:nth-child(4),
  .search-form .recommend .tag:nth-child(5){margin-top: 2.78vw;}
  .search-form .recommend .tag a{padding: 0 3.33vw; height: 8.33vw; font-size: 3.06vw; border-radius: 4.17vw;}

  .search-form .sns-con{flex-direction: column; margin-top: 6.94vw;}
  .search-form .sns-con strong{margin-right: 0; margin-bottom: 4.17vw; font-size: 3.33vw;}
  .search-form .sns-con .sns-list{margin-top: 0;}
  .search-form .sns-con .sns-list li + li{margin-left: 4.17vw;}
  .search-form .sns-con .sns-list li a{width: 8.33vw; height: 8.33vw;}

  //inp-radio-con
  .inp-radio-con{flex-wrap: wrap;}
  .inp-radio-con .inp-wrap{width: 100%;}
  .inp-radio-con .inp-con + .inp-con{margin-top: 5.56vw;}
  .inp-radio-con .inp-con .inp-radio{margin-bottom: 2.78vw; min-width: 23.61vw;}
  .inp-radio-con .inp-con .btn-con .btn-line{margin-top: 0; padding: 0 3.61vw;}

  .inp-radio .inp-label{font-size: 3.61vw;}
  .inp-radio .inp-label::before{margin-right: 2.78vw; width: 4.17vw; height: 4.17vw;}

  .inp-radio .txt{margin-top: 1vw; padding-left: 7.17vw; font-size: 2.78vw;}
}

