@charset "UTF-8";

@import "../base";

body.popView{overflow: hidden; height: 100vh;}

.popup-wrap{display: none; overflow: auto hidden; z-index: 999; position: fixed; left: 0; top: 0; width: 100%; height: 100%; background-color: rgba(0,0,0,0.8);}
.popup-wrap.open{@include flex('center');}

.popup-frame{position: relative; overflow: hidden auto; max-width: 1200px; max-height: 80%; width: 90%; border-radius: 20px; -ms-overflow-style: none; scrollbar-width: none;}
.popup-frame::-webkit-scrollbar {display: none;}

.popup-header{position: sticky; top: 0; @include flex('space-between'); padding: 20px 40px; width: 100%; min-height: 60px; background-color: $c-black; z-index: 5;}
.popup-header .tit{font-size: 24px; font-weight: 700; color: $c-white;}
.popup-header .btn-close{width: 32px; height: 32px; @include bg('/common/ico_close.svg');}
.popup-con{width: 100%; padding: 60px; background-color: $c-bg;}

.alert{z-index: 9999; display: none; position: fixed; left: 0; top: 0; padding: 0 40px; width: 100%; height: 100%; background-color: rgba(0,0,0,0.5);}
.alert.open{display: block;}
.alert .inner{display: inline-block; position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); padding: 80px 35px 35px; min-width: 520px; background-color: $c-white; border-radius: 20px;}
.alert .inner.fixed{position: relative; left: 0; top: 0; transform: translate(0, 0); margin: 0 auto;}
.alert .inner .close_btn{position: absolute; right: 35px; top: 30px; width: 30px;}
.alert .inner .detail-con .tit{text-align: center; font-size: 22px; font-weight: 700; line-height: 1.5;}
.alert .inner .detail-con .btn{margin-top: 40px; width: 240px; height: 50px; border-radius: 30px;}

.data-add-pop .popup-con{padding: 40px; min-height: 320px;}
.data-add-pop .search-form.white-box{padding: 40px;}
.data-add-pop .search-form .tit-l{margin-bottom: 20px;}
.data-add-pop .product-add-con{margin-left: 20px; padding-left: 20px; border-left: 1px solid #ddd;}
.data-add-pop section + section{margin-top: 40px;}
.data-add-pop .popup-frame{max-height: 90%;}
.data-add-pop .product-list-wrap{overflow: auto; padding-right: 10px; height: 370px;}
.data-add-pop .paging{padding-top: 10px;}
//.data-add-pop .product-list-wrap::-webkit-scrollbar{width: 6px; background: transparent;}
//.data-add-pop .product-list-wrap::-webkit-scrollbar-thumb{padding-left: 10px; background: #999; border-radius: 10px;}
//.data-add-pop .product-list-wrap::-webkit-scrollbar-track{background: transparent;}

@media (max-width: 1450px) {
  .data-add-pop .paging{padding-top: 30px;}
  .data-add-pop .product-add-con{margin-left: 0; padding-left: 0; border: none;}

}

@media (max-width: 720px) {
  .popup-header{padding: 4.17vw; min-height: 12.50vw;}
  .popup-header .tit{font-size: 5.00vw;}
  .popup-header .btn-close{width: 8.33vw; height: 8.33vw;}

  .popup-con{padding: 8.33vw 4.17vw;}

  .alert{padding: 0 5.56vw;}
  .alert .inner{padding: 11.11vw 4.86vw 4.86vw; min-width: auto; width: calc(100% - 11.11vw); border-radius: 2.78vw;}
  .alert .inner .close_btn{right: 4.86vw; top: 4.17vw; width: 5.56vw;}
  .alert .inner .detail-con .tit{font-size: 4vw;}
  .alert .inner .detail-con .btn{margin-top: 5.56vw; width: 44.44vw; height: 11.11vw; font-size: 3vw; border-radius: 8vw;}

  .data-add-pop .paging{padding-top: 4.17vw;}
}