@charset "UTF-8";

@import "../base";

body.popView{overflow: hidden; height: 100vh;}

/* wrap */
.wrap{position: relative; padding: 80px 0 140px; width: 100%; height: 100vh;}

/* frame, contents */
.frame{@include flex('center', ''); position: relative; height: 100%; background-color: $c-bg;}
.contents{overflow: auto; margin: 0 auto; padding: 30px 40px 80px; width: 100%; max-width: 1680px; height: 100%; -ms-overflow-style: none; scrollbar-width: none;}
.contents.space-l{padding-top: 84px;}
.contents::-webkit-scrollbar{display: none;}

section + section{margin-top: 60px;}

/* header */
.header{position: fixed; top: 0; left: 0; @include flex('center'); padding: 0 60px; width: 100%; height: 80px; background-color: $c-black; z-index: 10;}
.header .logo a{display: block; margin-right: 80px; flex-shrink: 0; width: 120px;}

.menu-list{@include flex(); height: 100%;}
.menu-list .item{position: relative; height: 100%;}
.menu-list .item + .item{margin-left: 80px;}
.menu-list .item h2{color: $c-gray; font-size: 18px; line-height: 80px; font-weight: 700;}
.menu-list .item h2:hover{color: $c-white;}
.menu-list .item .sub-list{display: none; position: absolute; left: 50%; transform: translateX(-50%); padding-top: 10px; width: 210px;}
.menu-list .item .sub-list .sub-menu{padding: 30px 20px; width: 100%; color: $c-gray; background-color: $c-black; border-radius: 20px;}
.menu-list .item .sub-list .sub-menu > li{margin-bottom: 20px;}
.menu-list .item .sub-list .sub-menu > li:last-child{margin-bottom: 0;}
.menu-list .item .sub-list .sub-menu h3{color: #fff; font-size: 18px; font-weight: 700;}
.menu-list .item .sub-list .sub-menu .list{margin: 14px 0; font-size: 16px; font-weight: 500; line-height: 1.6;}
.menu-list .item .sub-list .sub-menu .list li:hover{color: #fff;}

.menu-list .item h2.active{color: $c-white;}
.menu-list .item .sub-list.active{display: block;}

.util-menu{@include flex(); margin-left: auto;}
.util-menu a{display: inline-flex; align-items: center;}
.util-menu a::after{content: ''; display: inline-block; margin: 0 20px; width: 1px; height: 20px; background-color: $c-white; opacity: 0.5;}
.util-menu a:last-child::after{display: none;}
.util-menu a .ico{margin-right: 10px;}
.util-menu .alarm-wrap{position: relative;}
.util-menu .alarm span{display: inline-flex; align-items: center; padding: 0 8px; height: 20px; color: $c-white; font-size: 14px; font-weight: 700; background-color: $c-red; border-radius: 20px;}
.util-menu .alarm-con{display: none; overflow: hidden; position: absolute; top: 60px; left: 50%; transform: translateX(-50%); width: 300px; background: $c-white; z-index: 3; border-radius: 20px; box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);}
.util-menu .alarm-con.active{display: block;}
.util-menu .alarm-con > .tit{@include flex('space-between'); padding: 20px 20px 10px; font-size: 18px; font-weight: 700;}
.util-menu .alarm-con > .tit .btn-more{font-size: 12px; font-weight: 500;}
.util-menu .alarm-con > .tit .btn-more::after{content: ''; display: inline-block; margin: 0; width: 16px; height: 16px; @include bg('/common/ico_arrow_right.svg'); opacity: 1;}
.util-menu .alarm-con .alarm-list .item{cursor: pointer;}
.util-menu .alarm-con .alarm-list .item dt{position: relative; padding: 12px 40px 12px 20px; font-size: 14px; font-weight: 500; border-top: 1px solid $c-gray4;}
.util-menu .alarm-con .alarm-list .item dt::after{content: ''; position: absolute; top: 50%; right: 20px; transform: translateY(-50%); display: block; width: 16px; height: 16px; @include bg('/common/ico_arrow_down.svg');}
.util-menu .alarm-con .alarm-list .item dt .tit{overflow: hidden; position: relative; display: -webkit-box; padding-top: 2px; -webkit-line-clamp: 1; -webkit-box-orient: vertical; word-break: keep-all; white-space: normal; text-overflow: ellipsis;}
.util-menu .alarm-con .alarm-list .item dt .date{display: block; margin-top: 8px; font-family: $roboto;}
.util-menu .alarm-con .alarm-list .item dd{display: none; position: relative; padding: 12px 20px; font-size: 14px;}
.util-menu .alarm-con .alarm-list .item dd::before{content: ''; position: absolute; top: 0; left: 0; display: block; width: 100%; height: 1px; background: $c-gray4;}

.util-menu .alarm-con .alarm-list .item.new .tit::before{content: ''; position: absolute; top: 0; left: 0; width: 6px; height: 6px; background: $c-red; border-radius: 50%;}
.util-menu .alarm-con .alarm-list .item.read dt{color: $c-gray2;}

.util-menu .alarm-con .alarm-list .item.active{background: $c-bg;}
.util-menu .alarm-con .alarm-list .item.active dt::after{transform: translateY(-50%) rotate(-180deg);}

.util-menu .user{color: $c-white; font-size: 18px;}

.mob-menu{display: none;}

/* nav */
.nav{overflow: hidden auto; padding: 40px 20px 0 0; flex-shrink: 0; width: 240px; height: 100%; background-color: $c-white; border-right: 1px solid $c-gray4;}
.nav .nav-list li h3{@include flex(); padding-left: 30px; width: 100%; height: 40px; font-size: 16px; font-weight: 700; color: $c-gray2;}
.nav .nav-list li.active h3{position: relative; color: $c-primary;}
.nav .nav-list li.active h3::before{content: ''; display: block; position: absolute; top: 0; left: 0; width: 5px; height: 100%; background-color: $c-primary;}
.nav .nav-list .list{padding: 12px 30px 20px;}
.nav .nav-list .list li + li{margin-top: 10px;}
.nav .nav-list .list li a{display: inline-block; width: 100%; font-size: 14px; font-weight: 500; color: $c-gray2; line-height: 1.4;}
.nav .nav-list .list li.active a{color: $c-primary;}


/* footer */
.footer{position: relative; @include flex('center'); flex-direction: column; height: 140px; background-color: #444; text-align: center;}
.footer .link-list{@include flex(); color: #fff; font-size: 14px;}
.footer .link-list li::after{content: ''; display: inline-block; margin: 0 16px; width: 1px; height: 12px; background-color: $c-gray;}
.footer .link-list li:last-child::after{display: none;}
.footer .address{@include flex(); margin-top: 20px; color: $c-gray2; font-size: 12px;}
.footer .address li + li{margin-left: 24px;}
.footer .address .link{color: #fff; text-decoration: underline;}
.footer .copyright{margin-top: 6px; color: $c-gray2; font-size: 12px;}


@media (max-width: 1400px) {

}

@media (max-width: 1200px) {
  .header{padding: 0 40px;}
  .header .logo a{margin-right: 40px; width: 100px;}
  .menu-list .item + .item{margin-left: 60px;}
}

@media (max-width: 960px) {
  .wrap{padding: 80px 0 0;}
  .frame{height: fit-content;}

  /* header */
  .menu-list{display: none;}
  .util-menu{display: none;}
  .header .mo-only{display: block !important;}
  .header .logo a{margin-right: 0;}
  .header .ico-menu{display: block; margin-left: auto; width: 40px; height: 40px; @include bg('common/ico_menu.svg');}

  .mob-menu{display: none; overflow: hidden auto; position: fixed; top: 0; left: 0; right: 0; bottom: 0; background-color: $c-black;}
  .mob-menu .inner{padding: 0 40px;}
  .mob-menu .top{@include flex('space-between'); height: 80px;}
  .mob-menu .top .logo a{width: 100px;}
  .mob-menu .top .ico-close{display: block; width: 40px; height: 40px; @include bg('common/ico_close.svg');}

  .mob-menu .util-menu{@include flex('space-between'); margin-top: 10px; padding: 20px; background: $c-white; border-radius: 20px;}
  .mob-menu .util-menu a .ico{margin-right: 10px;}
  .mob-menu .util-menu .user{color: $c-black; font-size: 18px;}
  .mob-menu .util-menu .ico-user{width: 20px; height: 20px; background-image: url("../img/common/ico_user_b.svg");}
  .mob-menu .util-menu a::after{display: none;}
  .mob-menu .util-menu .ico-alarm{width: 30px; height: 30px; background-image: url("../img/common/ico_alarm_b.svg");}
  .mob-menu .util-menu .alarm span{padding: 0 8px; height: 20px; font-size: 14px;}

  .mob-menu .menu{margin: 40px 0 60px;}
  .mob-menu .menu .item{color: $c-gray;}
  .mob-menu .menu .item h2{@include flex('space-between'); padding: 20px 0; font-size: 30px; font-weight: 500; border-bottom: 1px solid rgba(68, 68, 68, 0.5);}
  .mob-menu .menu .item h2::after{content: ''; display: inline-block; width: 40px; height: 40px; @include bg('/common/ico_arrow_down_w.svg');}
  .mob-menu .menu .item h2.active{color: $c-white;}
  .mob-menu .menu .item h2.active::after{transform: rotate(180deg);}
  .mob-menu .menu .item .sub-menu{display: none; padding-bottom: 50px;}
  .mob-menu .menu .item .sub-menu li h3{@include flex('space-between'); padding: 20px 0; font-size: 18px; font-weight: 700; border-bottom: 1px solid rgba(68, 68, 68, 0.5);}
  .mob-menu .menu .item .sub-menu li h3.ico-more::after{content: ''; display: inline-block; width: 30px; height: 30px; @include bg('/common/ico_arrow_down_w.svg');}
  .mob-menu .menu .item .sub-menu li h3.active{color: $c-white;}
  .mob-menu .menu .item .sub-menu li h3.active::after{transform: rotate(180deg);}
  .mob-menu .menu .item .sub-menu .list{display: none; padding: 20px 0; border-bottom: 1px solid rgba(68, 68, 68, 0.5);}
  .mob-menu .menu .item .sub-menu .list li + li{margin-top: 4px;}
  .mob-menu .menu .item .sub-menu .list li a{display: block; padding: 6px 0; font-size: 14px; font-weight: 500; line-height: 1.6; }

}

@media (max-width: 720px) {
  .wrap{padding: 15.44vw 0 0;}

  .contents{padding: 11.11vw 4.17vw;}
  .contents.space-l{padding-top: 11.11vw;}
  section + section{margin-top: 11.11vw;}

  /* header */
  .header{padding: 0 4.17vw; height: 15.44vw;}
  .header .logo a{width: 22.00vw;}
  .header .ico-menu{display: block; margin-left: auto; width: 8.33vw; height: 8.33vw; @include bg('common/ico_menu.svg');}

  .mob-menu{display: none; overflow: hidden auto; position: fixed; top: 0; left: 0; right: 0; bottom: 0; background-color: $c-black;}
  .mob-menu .inner{padding: 0 4.17vw;}
  .mob-menu .top{@include flex('space-between'); height: 15.44vw;}
  .mob-menu .top .logo a{margin-right: 0; width: 22.00vw;}
  .mob-menu .top .ico-close{display: block; width: 8.33vw; height: 8.33vw; @include bg('common/ico_close.svg');}

  .mob-menu .util-menu{@include flex('space-between'); margin-top: 1.39vw; padding: 4.17vw; background: $c-white; border-radius: 2.78vw;}
  .mob-menu .util-menu a .ico{margin-right: 1.67vw;}
  .mob-menu .util-menu .user{color: $c-black; font-size: 3.89vw;}
  .mob-menu .util-menu .ico-user{width: 4.17vw; height: 4.17vw; background-image: url("../img/common/ico_user_b.svg");}
  .mob-menu .util-menu a::after{display: none;}
  .mob-menu .util-menu .ico-alarm{width: 5.56vw; height: 5.56vw; background-image: url("../img/common/ico_alarm_b.svg");}
  .mob-menu .util-menu .alarm span{padding: 0 2.22vw; height: 4.44vw; font-size: 3.33vw;}

  .mob-menu .menu{margin: 8.33vw 0 11.11vw;}
  .mob-menu .menu .item h2{padding: 4.17vw 0; font-size: 5.56vw; border-bottom-width: 0.28vw;}
  .mob-menu .menu .item h2::after{width: 5.56vw; height: 5.56vw;}
  .mob-menu .menu .item .sub-menu li h3{padding: 4.17vw 0; font-size: 3.89vw;  border-bottom-width: 0.28vw;}
  .mob-menu .menu .item .sub-menu li h3.ico-more::after{width: 4.17vw; height: 4.17vw;}
  .mob-menu .menu .item .sub-menu .list{padding: 4.17vw 0;  border-bottom-width: 0.28vw;}
  .mob-menu .menu .item .sub-menu .list li + li{margin-top: 1.39vw;}
  .mob-menu .menu .item .sub-menu .list li a{padding: 1.39vw 0; font-size: 3.33vw; line-height: 1.6;}

  /* footer */
  .footer{padding: 8.33vw 4.17vw; height: 48.61vw;}
  .footer .link-list{font-size: 3.33vw;}
  .footer .link-list li::after{margin: 0 3.33vw; width: 0.28vw; height: 3.06vw;vertical-align: middle;}
  .footer .address{flex-wrap: wrap; justify-content: center; margin-top: 5.56vw; font-size: 3.06vw;}
  .footer .address li + li{margin-left: 0; margin-top: 2.22vw;}
  .footer .address li:last-child{margin-left: 3.33vw;}
  .footer .copyright{margin-top: 3.33vw; font-size: 3.06vw;}
}












